<template>
  <section class="card p10">
    <!--      info (  )   ,    ( )   main-->
    <div class="dateContainer">
      <span class="grey mr20">date:</span>
      <datetime format="YYYY-MM-DD H:i:s" v-model='datetimeInput' width="280px" style="display: inline-block"/>
      <span class="ml20 grey mr20">comment:</span>
      <input type="text" v-model='comment' class="commentInput mr10" v-on:keyup.enter="(ev) => {ev.target.blur();addEventAlarm()}">
      <button @click='addEventAlarm' class="addBtn">add</button>
      <span class="ml20 fz12 grey">({{cTime}})</span>
    </div>
  </section>
</template>

<script>
  import generateRandomId from '../../helpers/generateRandomId';
  import Popup from '../Msg';
  import datetime from 'vuejs-datetimepicker';
  export default {
    components: { Popup, datetime },
    data() {
      return {
        datetimeInput:'',
        comment:null,
      };
    },
    computed: {
      direction() {
        if (this.radioDirection) {
          return -1
        } else {
          return 1
        }
      },
      cTime(){
        return new Date(this.datetimeInput).toLocaleString()
      }

    },
    methods: {
      addEventAlarm () {
        const item = {
          comment:this.comment,
          id:generateRandomId(),
          date:this.cTime,
        }
        if(this.datetimeInput&&this.comment){
          this.$store.commit('addAlarm', {
            item,
            listName:'alarmEventsList',
            info:this.$store.state.isAK?'infoAK':'infoMain'
          })
          this.comment = null;
        }
      },
    }
  }

</script>

