    color: rgba(255, 255, 255, 0.5);
    background-color: rgba(0,0,0, 0.8);
    margin-left: 10px;
  }
  .addBtn:hover{
    cursor: pointer;
    color: rgba(255, 255, 255, 0.8);
  }
  .dateInput{
    width: 300px;
  }
  .commentInput{
    width: 400px;
