<template>
  <ul class="card p30">
    <AlarmDatePickerPanel/>
    <AlarmListItem v-if="eventsList" list-name="alarmEventsList" v-for="item in eventsList" :key="item.id" :item="item"/>
    <AlarmListItem v-if="computedAltsList" list-name="alarmAltsList" v-for="item in computedAltsList" :key="item.id" :item="item"/>
  </ul>
</template>

<script>
  import AlarmListItem from './AlarmListItem';
  import AlarmDatePickerPanel from './AlarmDatePickerPanel';
  export default {
    components: { AlarmDatePickerPanel, AlarmListItem },
    props:['altsList','eventsList'],
    computed:{
      computedAltsList(){//   
        const newList = this.altsList;

        return newList.sort(function (a, b) {
          if (a['coin'] > b['coin']) {
            return 1;
          }
          if (a['coin'] < b['coin']) {
            return -1;
          }
          // a    b
          return 0;
        });
      },
    }

  };
</script>

