<template>
  <li>
    <div class="alarmListItem pr" v-if="listName==='alarmAltsList'">
      <img
        src="/img/alarmItem.png" alt="alarm ico"
        class="alarmClockImg"
        v-if="item.currentPrice<=item.desiredPrice&&item.amount>0 || item.currentPrice>=item.desiredPrice&&item.amount<0"
      />
      <div class="symbolBox">
        <img :src="item.coinUrl" style='width:100%' alt="coin ico"/>
      </div>
      <div class="coinBox">
        <div class="name">{{item.name}}</div>
        <div>{{item.coin?item.coin.toUpperCase():''}}</div>
      </div>
      <div class="numberBox" :class="{'lightGreen':item.amount>0,'lightRed':item.amount<0}">{{item.amount<0?'sell':'buy'}}</div>
      <div class="amBox"><span class="grey label">amount: </span>{{item.amount | numFormat('0,0.000')}}</div>
      <div class="prBox"><span class="grey label">price: </span>{{item.desiredPrice | numFormat('$ 0,0.00')}}</div>
      <div class="prBox curr"><span class="grey label">cPrice: </span>{{item.currentPrice| numFormat('$ 0,0.000')}}</div>
      <div class="dateBox"><span class="grey label">date: </span>{{item.date}}</div>
      <form @submit.prevent="addComment">
        <span class="grey label">comment: </span>
        <input type="text" v-model="item.comment" class="inputBox" v-on:keyup.enter="(ev) => ev.target.blur()">
      </form>
      <button @click="deleteItem(item.id)" class="delBox">x</button>
    </div>
    <div class="alarmListItem pr" v-if="listName==='alarmEventsList'">
      <img
        src="/img/alarmItem.png" alt="alarm ico"
        class="alarmClockImg"
        v-if="new Date(item.date)<new Date($store.state.updateDate)"
      />
      <div class="dateEventBox">
        <span class="grey label">date of alarm: </span>
        {{item.date}}
      </div>
      <div class="commentBox">
        <span class="grey label">comment: </span>
        <input
          type="text"
          v-model="item.comment"
          class="inputBoxEvent"
          v-on:keyup.enter="(ev) => {ev.target.blur();addComment()}"
        >
      </div>

      <button @click="deleteItem(item.id)" class="delBox">x</button>
    </div>
  </li>
</template>

<script>
  export default {
    props:['item','listName'],
    methods: {
      addComment(){
        this.$store.dispatch('updateFB',{name:this.listName,info:this.$store.state.isAK?'infoAK':'infoMain'});
      },
      deleteItem(id){
        this.$store.commit('deleteItem', { id, listName:this.listName})
      },
    },
  };
</script>

